import React, { useEffect, useState } from 'react';
import { EnumState, KeyOfIState } from '../../constance';
export default (props) => {
    const { store, params, limit } = props;
    const MeetingList = store.MeetingList;
    return (
        <React.Fragment>
            <MeetingList
                height={props.height}
                params={params}
                limit={limit}
                onFetch={store.fetchWillSignInList}
            />
        </React.Fragment>
    );
};
