/* eslint-disable prefer-const */
import React, { useRef, useEffect, useState } from "react";
import dayjs from "dayjs";
import { observer, inject } from "mobx-react";
import { Tabs, WhiteSpace, Icon } from "antd-mobile";
import MeetingWillSignIn from "./components/MeetingWillSignIn";
import { useHistory } from "react-router-dom";
import EasyNarBar from "@/components/EasyNarBar";
import storage from "@/utils/storage";

const renderMeetingTab = (props: any) => {
  const lv = useRef(null);
  const {
    userInfoStore: { userMemberInfo }
  } = props;
  const now = dayjs().format("YYYY-MM-DD HH:mm:ss");
  const [height, setHeight] = useState(0);
  const { WorkNumber } = userMemberInfo || {};
  const tabs = [{ title: "待签到" }, { title: "已签到" }, { title: "未签到" }];
  let initialPage = 0;
  if (storage.getItem("meetingNotificationTab")) {
    let tab = Number(storage.getItem("meetingNotificationTab"));
    if (tab >= 0) {
      initialPage = tab;
      storage.removeItem("meetingNotificationTab");
    }
  }

  useEffect(() => {
    let barDom = document.querySelector(".am-tabs-tab-bar-wrap");
    let navbarDom = document.querySelector(".am-navbar");
    setHeight(
      Math.max(document.documentElement.clientHeight, document.body.clientHeight) -
        (barDom ? barDom.offsetHeight : 0) -
        (navbarDom ? navbarDom.offsetHeight : 0) -
        10
    );
  }, []);

  return (
    <div ref={lv}>
      <Tabs tabs={tabs} initialPage={initialPage} animated={false} useOnPan={false}>
        <div className="meeting-notification-tab-item">
          {/* 待签到 =  未过期，而且还没签到 */}
          {WorkNumber && (
            <MeetingWillSignIn
              height={height}
              store={props.store}
              params={{
                filterCondition: [
                  {
                    type: "1",
                    field: "meeting_info_wx_view.WorkNumber",
                    condition: "=",
                    connection: "&&",
                    id: 1,
                    value: WorkNumber // 登陆用户的ID
                  },
                  {
                    type: "1",
                    field: "meeting_info_wx_view.endTime",
                    condition: ">",
                    connection: "&&",
                    id: 2,
                    value: now
                  },
                  {
                    type: "1",
                    field: "meeting_info_wx_view.singinType",
                    condition: "=",
                    connection: "&&",
                    id: 3,
                    value: "0"
                  }
                ],
                orderBy: [
                  {
                    fieldName: "meeting_info_wx_view.startTime",
                    sort: "ASC"
                  }
                ]
              }}
              limit={20}
            />
          )}
        </div>
        <div className="meeting-notification-tab-item">
          {/* 已签到 = 已经签到的数据 */}
          {WorkNumber && (
            <MeetingWillSignIn
              height={height}
              store={props.store}
              params={{
                filterCondition: [
                  {
                    type: "1",
                    field: "meeting_info_wx_view.WorkNumber",
                    condition: "=",
                    connection: "&&",
                    id: 1,
                    value: WorkNumber // useId
                  },
                  {
                    type: "1",
                    field: "meeting_info_wx_view.singinType",
                    condition: "=",
                    connection: "&&",
                    id: 2,
                    value: "1"
                  }
                ],
                orderBy: [
                  {
                    fieldName: "meeting_info_wx_view.startTime",
                    sort: "DESC"
                  }
                ]
              }}
              limit={20}
            />
          )}
        </div>
        <div className="meeting-notification-tab-item">
          {/* 未签到 = 已过期，并尚未签到的数据 */}
          {WorkNumber && (
            <MeetingWillSignIn
              height={height}
              store={props.store}
              params={{
                filterCondition: [
                  {
                    type: "1",
                    field: "meeting_info_wx_view.WorkNumber",
                    condition: "=",
                    connection: "&&",
                    id: 1,
                    value: WorkNumber // userId
                  },
                  {
                    type: "1",
                    field: "meeting_info_wx_view.endTime",
                    condition: "<",
                    connection: "&&",
                    id: 2,
                    value: now
                  },
                  {
                    type: "1",
                    field: "meeting_info_wx_view.singinType",
                    condition: "=",
                    connection: "&&",
                    id: 3,
                    value: "0"
                  }
                ],
                orderBy: [
                  {
                    fieldName: "meeting_info_wx_view.startTime",
                    sort: "DESC"
                  }
                ]
              }}
              limit={20}
            />
          )}
        </div>
      </Tabs>
      <WhiteSpace />
    </div>
  );
};

export default inject("userInfoStore")(
  observer((props: any) => {
    const history = useHistory();

    useEffect(() => {
      const {
        userInfoStore: { fetchUserInfo }
      } = props;
      fetchUserInfo();
    }, []);

    return (
      <>
        <EasyNarBar
          mainContent="会议通知"
          leftContent={<Icon type="left" />}
          onLeftClick={() => {
            history.push({ pathname: "/dashboard" });
          }}
        ></EasyNarBar>

        {renderMeetingTab(props)}
      </>
    );
  })
);
