import React, { useEffect, useState, useRef } from "react";
import ReactDOM from "react-dom";
import dayjs from "dayjs";
import { ListView, Icon, Toast } from "antd-mobile";
import { Link } from "react-router-dom";

const MeetingList = props => {
  const lv = useRef(null);
  const [skip, setSkip] = useState(0);
  const [hasMore, setHasMore] = useState(true);
  const [isLoading, setIsLoading] = useState(false);
  const [data, setData] = useState([]);
  const [dataSource, setDataSource] = useState(
    new ListView.DataSource({
      rowHasChanged: (row1, row2) => row1 !== row2
    })
  );

  useEffect(() => {
    getData();
  }, []);

  const getData = () => {
    const { store } = props;
    setIsLoading(true);
    store
      .fetchData(skip)
      .then(res => {
        let newData = [...data, ...res.aaData];
        setSkip(preSkip => preSkip + props.limit);
        setData(newData);
        setDataSource(dataSource.cloneWithRows(newData));
        setHasMore(newData.length >= res.iTotalRecords ? false : true);
      })
      .catch(err => {
        Toast.info("请求失败", 1);
      })
      .finally(() => {
        setIsLoading(false);
      });
  };

  const onEndReached = () => {
    if (!isLoading && hasMore) {
      getData();
    }
  };

  const isSameDate = (start, end) => {
    const startFormated = dayjs(start).format("YYYY-MM-DD");
    const endFormated = dayjs(end).format("YYYY-MM-DD");
    return startFormated === endFormated;
  };

  const renderMeetinglistTime = (start, end) => {
    const isSame = isSameDate(start, end);
    if (isSame) {
      return dayjs(start).format("YYYY-MM-DD HH:mm") + " - " + dayjs(end).format("HH:mm");
    } else {
      return dayjs(start).format("YYYY-MM-DD HH:mm") + " - " + dayjs(end).format("YYYY-MM-DD HH:mm");
    }
  };

  const renderRow = function (item) {
    return (
      <div key={item.meeting_info_wx_view_id} className="meetinglist-item">
        <Link className="meetinglist-link" to={`/meeting-notification/${item.meeting_info_wx_view_id}`}>
          <div className="meetinglist-content">
            <div className="meetinglist-title">{item.meeting_info_wx_view_name}</div>
            <div className="meetinglist-brief">
              <div>
                <div className="meetinglist-time">
                  {(() => {
                    if (item.meeting_info_wx_view_startTime && item.meeting_info_wx_view_endTime) {
                      return renderMeetinglistTime(item.meeting_info_wx_view_startTime, item.meeting_info_wx_view_endTime);
                    } else {
                      return null;
                    }
                  })()}
                </div>
                <div>
                  {(() => {
                    if (Number(item.meeting_info_wx_view_type) === 0) {
                      return `地点: ${item.meeting_info_wx_view_roomName || ""}`;
                    } else {
                      return `地点: ${item.meeting_info_wx_view_address || ""}`;
                    }
                  })()}
                </div>
              </div>
            </div>
          </div>
          <Icon className="meetinglist-arrow" type="right" size="sm" />
        </Link>
      </div>
    );
  };

  return (
    <div ref={lv} style={{ width: "100%" }}>
      <ListView
        dataSource={dataSource}
        renderFooter={() => <div className="meetinglist-tip">{isLoading ? "加载中..." : data.length > 0 ? "加载完成" : "暂无数据"}</div>}
        renderRow={renderRow}
        pageSize={props.limit || 20}
        style={{
          height: props.height
        }}
        scrollRenderAheadDistance={500}
        onEndReached={onEndReached}
        onEndReachedThreshold={10}
      />
    </div>
  );
};

export default MeetingList;
