import { IMeetingItem } from "./interface";

export default class Store {
  constructor(props: any) {
    this.historyContext = props.historyContext;
    this.params = props.params;
    this.limit = props.limit || 20;
    this.data = props.data;
    this.onFetch = props.onFetch;
    this.onSuccess = props.onSuccess;
    this.onFail = props.onFail;
  }
  historyContext: any;
  params: any;
  limit: number;
  data: IMeetingItem[];
  onFetch: (skip: number, limit: number, params: any) => Promise<any>;
  onSuccess: (data: IMeetingItem[]) => void;
  onFail: (err: Error) => void;

  fetchData(skip: any): Promise<any> {
    return this.onFetch(skip, this.limit, this.params);
  }
}
