import React, { useContext } from 'react';
import { MeetingListStore } from './store';
import MeetingNotificationClass from './list/MeetingNotification';
import MeetingList from '@/components/MeetingList';
import { TokenContext } from '@/pages/_internal/app/middlewares/AppAuthorized';
import { inject } from 'mobx-react';
require('./list/style.scss');

const MeetingNotification = inject('userInfoStore')((props: any) => {
    const tokenContext = useContext(TokenContext);
    const meetingListStore = new MeetingListStore({
        history: props.history,
        MeetingList,
        tokenContext,
    });
    return (
        <MeetingNotificationClass
            store={meetingListStore}
            userInfoStore={props.userInfoStore}
        />
    );
});

export default MeetingNotification;
