import React from 'react';
import Store from './Store';
import MeetingList from './MeetingListV2';
import { observer } from 'mobx-react';
import { HistoryContext } from '@/router';
require('./style.scss');

export default observer((props) => {
    const store = new Store({ history: HistoryContext, ...props });
    return <MeetingList store={store} limit={props.limit} height={props.height} />;
});
